'use client';

import React, { useState } from 'react';
import {
  Box,
  Button,
  Menu,
  MenuItem,
  Typography,
  Chip,
  ListItemIcon,
  ListItemText,
  Divider
} from '@mui/material';
import { Store, ChevronDown } from 'lucide-react';
import { useTenant } from '../contexts/TenantContext';

const TENANT_OPTIONS = [
  {
    id: 'restaurant1',
    name: 'Green Garden Organic',
    brandName: 'Green Garden',
    color: '#10b981',
    emoji: '🌿',
    deliveryFee: 39,
    freeThreshold: 200
  },
  {
    id: 'restaurant2',
    name: 'Zen Healthy Treats',
    brandName: 'Zen Treats',
    color: '#7c3aed',
    emoji: '🟣',
    deliveryFee: 49,
    freeThreshold: 250
  },
  {
    id: 'restaurant3',
    name: 'Fresh Bowl Co.',
    brandName: 'Fresh Bowl',
    color: '#f59e0b',
    emoji: '🟠',
    deliveryFee: 35,
    freeThreshold: 180
  }
];

interface TenantSwitcherProps {
  variant?: 'button' | 'chip';
  size?: 'small' | 'medium' | 'large';
}

export default function TenantSwitcher({ variant = 'button', size = 'medium' }: TenantSwitcherProps) {
  const { tenant, tenantId } = useTenant();
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const currentTenant = TENANT_OPTIONS.find(t => t.id === tenantId);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleTenantChange = (newTenantId: string) => {
    // เปลี่ยน URL parameter
    const url = new URL(window.location.href);
    url.searchParams.set('tenant', newTenantId);
    window.location.href = url.toString();
    handleClose();
  };

  if (!currentTenant) return null;

  if (variant === 'chip') {
    return (
      <>
        <Chip
          label={
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
              <span>{currentTenant.emoji}</span>
              <span>{currentTenant.brandName}</span>
              <ChevronDown size={14} />
            </Box>
          }
          onClick={handleClick}
          sx={{
            backgroundColor: `${currentTenant.color}20`,
            color: currentTenant.color,
            border: `1px solid ${currentTenant.color}40`,
            '&:hover': {
              backgroundColor: `${currentTenant.color}30`,
            }
          }}
        />
        <Menu
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'left',
          }}
        >
          {TENANT_OPTIONS.map((option) => (
            <MenuItem
              key={option.id}
              onClick={() => handleTenantChange(option.id)}
              selected={option.id === tenantId}
              sx={{
                minWidth: 280,
                '&.Mui-selected': {
                  backgroundColor: `${option.color}10`,
                }
              }}
            >
              <ListItemIcon>
                <Box
                  sx={{
                    width: 32,
                    height: 32,
                    borderRadius: '50%',
                    backgroundColor: `${option.color}20`,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    fontSize: '16px'
                  }}
                >
                  {option.emoji}
                </Box>
              </ListItemIcon>
              <ListItemText
                primary={
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="subtitle2" fontWeight="bold">
                      {option.brandName}
                    </Typography>
                    {option.id === tenantId && (
                      <Chip label="ปัจจุบัน" size="small" color="primary" />
                    )}
                  </Box>
                }
                secondary={
                  <Typography variant="caption" color="text.secondary">
                    ค่าส่ง ฿{option.deliveryFee} • ฟรีส่งครบ ฿{option.freeThreshold}
                  </Typography>
                }
              />
            </MenuItem>
          ))}
        </Menu>
      </>
    );
  }

  return (
    <>
      <Button
        variant="outlined"
        startIcon={<Store size={18} />}
        endIcon={<ChevronDown size={16} />}
        onClick={handleClick}
        size={size}
        sx={{
          borderColor: `${currentTenant.color}40`,
          color: currentTenant.color,
          backgroundColor: `${currentTenant.color}08`,
          '&:hover': {
            borderColor: currentTenant.color,
            backgroundColor: `${currentTenant.color}15`,
          }
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <span>{currentTenant.emoji}</span>
          <span>{currentTenant.brandName}</span>
        </Box>
      </Button>
      
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        PaperProps={{
          sx: {
            mt: 1,
            minWidth: 320,
            borderRadius: 2,
            boxShadow: '0 4px 20px rgba(0,0,0,0.1)',
          }
        }}
      >
        <Box sx={{ px: 2, py: 1.5 }}>
          <Typography variant="subtitle2" color="text.secondary" fontWeight="bold">
            เลือกร้านอาหาร
          </Typography>
        </Box>
        <Divider />
        
        {TENANT_OPTIONS.map((option) => (
          <MenuItem
            key={option.id}
            onClick={() => handleTenantChange(option.id)}
            selected={option.id === tenantId}
            sx={{
              px: 2,
              py: 1.5,
              '&.Mui-selected': {
                backgroundColor: `${option.color}10`,
              },
              '&:hover': {
                backgroundColor: `${option.color}08`,
              }
            }}
          >
            <ListItemIcon>
              <Box
                sx={{
                  width: 40,
                  height: 40,
                  borderRadius: '50%',
                  backgroundColor: `${option.color}20`,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontSize: '18px'
                }}
              >
                {option.emoji}
              </Box>
            </ListItemIcon>
            <ListItemText
              primary={
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                  <Typography variant="subtitle1" fontWeight="bold">
                    {option.brandName}
                  </Typography>
                  {option.id === tenantId && (
                    <Chip 
                      label="ปัจจุบัน" 
                      size="small" 
                      sx={{ 
                        backgroundColor: option.color,
                        color: 'white',
                        fontSize: '10px',
                        height: '20px'
                      }} 
                    />
                  )}
                </Box>
              }
              secondary={
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    {option.name}
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    💰 ค่าส่ง ฿{option.deliveryFee} • 🚚 ฟรีส่งครบ ฿{option.freeThreshold}
                  </Typography>
                </Box>
              }
            />
          </MenuItem>
        ))}
      </Menu>
    </>
  );
} 